$cell-size: 32px;
$cell-color: #e0e0e0;
$cell-hover: #eeeeee;
$cell-active: #bdbdbd;
$cell-border: #9e9e9e;
$cell-cover: #EEEEEE;
$cell-check: rgb(207, 216, 226);
.cell {
  width: $cell-size;
  height: $cell-size;
  background-color: $cell-color;
  // border: 2px solid $cell-border;
  box-shadow: 0 0 0 1px $cell-border, 0 0 0 2px $cell-active;
  line-height: $cell-size;
  text-align: center;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s;
  &:hover {
    background-color: $cell-hover;
  }
  &:active {
    background-color: $cell-active;
  }
}

.row {
  display: flex;
}

.cell-cover {
  background-color: $cell-cover;
  &:hover {
    background-color: $cell-cover;
  }
  &:active {
    background-color: $cell-cover;
  }
}

.cell-check {
  background-color: $cell-check;
  &:hover {
    background-color: $cell-check;
  }
  &:active {
    background-color: $cell-check;
  }
}

.cell-cnt {
  width: $cell-size;
  height: $cell-size;
}

.layout {
  margin: 24px 12px;
  overflow: auto;
}

.footer {
  margin-top: 16px;
  background-color: #fff;
  padding: 16px;
  text-align: center;
}

.cntr {
  background-color: #fff;
  padding: 24px;
  min-height: 280px;
  display: flex;
  justify-content: center; 
  flex-wrap: wrap;
}

.game-control {
  min-width: 150px;
  margin-right: 32px;
  margin-bottom: 32px;
}